<template>
  <el-card class="blog-user-center">
    <h2 class="blog-card--title"></h2>
    <h3></h3>
    <BaseForm ref="form" formType="userInfo" />
    <div class="user-center--control">
      <el-button type="success" @click="putUserInfo"></el-button>
      <el-button></el-button>
    </div>
  </el-card>
</template>

<script>
import BaseForm from '@/components/base/BaseForm.vue'

export default {
  name: 'VueBlogUser',
  components: { BaseForm },
  data() {
    return {}
  },
  created() {},
  computed: {
    nickname() {
      return this.$store.state.userInfo?.nickname
    }
  },
  methods: {
    putUserInfo() {
      const formRef = this.$refs['form']
      let formData = formRef.form

      formRef.$refs['elForm'].validate(async (valid) => {
        if (!valid) return false
        try {
          await this.$http({
            type: 'putUserInfo',
            data: formData
          })
          this.$store.dispatch('setUserInfo')
          this.$notify.success({
            title: '',
            message: ''
          })
        } catch (err) {
          //this.resetForm()
        }
      })
    }
  }
}
</script>

<style lang="stylus">
.blog-user-center
  h3
    padding 20px 30px
    font-size 20px

.user-center--control
  display flex
  justify-content flex-end
</style>

