<template>
  <div class="blog-editor">
    <el-card class="blog-editor--head">
      <h2 class="blog-card--title"></h2>
      <el-input
        ref="title"
        class="editor-head--input"
        placeholder="()"
        v-model="title"
        clearable
      ></el-input>
    </el-card>

    <el-card class="blog-editor--wrap">
      <div style="border: 1px solid #ebeef5">
        <Toolbar
          style="border-bottom: 1px solid #ebeef5"
          :editor="editor"
          :mode="mode"
        />
        <Editor
          class="wang-editor"
          style="height: 500px; overflow-y: hidden overflow-x: auto"
          v-model="html"
          :defaultConfig="editorConfig"
          :mode="mode"
          @onCreated="onCreated"
        />
      </div>
    </el-card>

    <el-card class="blog-editor--column">
      <h3></h3>
      <el-radio-group
        v-if="columnList.length"
        class="editor-column--group"
        v-model="columnSelected"
      >
        <el-radio-button
          v-for="column in columnList"
          :label="column.id"
          :key="column.id"
          >{{ column.name }}</el-radio-button
        >
      </el-radio-group>
    </el-card>

    <el-card class="blog-editor--control">
      <el-button type="primary" @click="submitArticle"></el-button>
      <el-button type="primary" @click="clearArticle"></el-button>
    </el-card>
  </div>
</template>

<script>
import { Editor, Toolbar } from '@wangeditor/editor-for-vue'
import '@wangeditor/editor/dist/css/style.css'
import store from 'store'
import HTTP_CONFIG from '@/configs/http.config'

const editorConfig = {
  placeholder: '...',
  MENU_CONF: {
    //
    uploadImage: {
      server: process.env.VUE_APP_BASE_UPLOAD_URL + '/article',
      //  2M
      maxFileSize: 5 * 1024 * 1024,
      //  10
      maxNumberOfFiles: 10,
      //  ['image/*']  []
      allowedFileTypes: ['jpg', 'jpeg', 'png', 'gif', 'bmp', 'webp'],
      //  http  header
      headers: {
        Authorization: `Bearer ${store.get(HTTP_CONFIG.TOKEN_NAME)}`
      },
      //
      // onBeforeUpload(file) {
      //   //  return
      //   // 1. return file  new  file 
      //   // 2. return false  file
      // },
      // // 
      // onFailed(file, res) {
      //   console.log(`${file.name} `, res)
      // },

      //  timeout 
      //(file, err, res)
      onError() {
        Vue.prototype.$notify.error({
          title: '',
          message: '(5M)'
        })
      }
    }
  }
}

export default {
  name: 'VueBlogEditor',
  components: { Editor, Toolbar },
  props: {
    columnId: {
      type: String
    }
  },
  data() {
    return {
      editor: null,
      html: '',
      title: '',
      cover: '',
      editorConfig: editorConfig,
      mode: 'default', // or 'simple'
      columnList: [],
      columnSelected: ''
    }
  },
  created() {
    //this.initCDN()
    //this.$nextTick(() => {
    // columnID 
    this.getColumns()
      .then(() => {
        this.columnId && (this.columnSelected = this.columnId)
      })
      .catch((err) => err)//console.log(err))
    //})
  },
  methods: {
    // initCDN() {
    //   const js = document.createElement('script')
    //   const css = document.createElement('link')
    //   js.src =
    //     'https://cdn.bootcdn.net/ajax/libs/wangeditor5/5.1.15/index.min.js'
    //   css.rel = 'stylesheet'
    //   css.href =
    //     'https://cdn.bootcdn.net/ajax/libs/wangeditor5/5.1.15/css/style.min.css'
    //   document.body.append(css, js)
    // },
    onCreated(editor) {
      this.editor = Object.seal(editor) //  Object.seal() 
    },
    async getColumns() {
      try {
        let { list } = await this.$http({ type: 'getColumns' })
        this.columnList = list.map(({ id, name }) => ({ id, name }))
        this.columnSelected = this.columnList?.[0].id
      } catch (error) {
        //
      }
    },

    //-
    async submitArticle() {
      if (!this.validateArticle()) return
      let content = this.editor.getHtml()
      try {
        await this.$http({
          type: 'postArticle',
          data: {
            title: this.title,
            // 
            cover: content.match(/<img src="([^\s]+)"/)?.[1] || undefined,
            column: this.columnSelected,
            content
          }
        })
        this.$router.push('/index')
        this.$notify.success({
          title: ''
        })
      } catch (err) {
        this.$notify.error({
          title: ''
        })
      }
    },

    validateArticle() {
      if (this.title.trim().length === 0) {
        this.$notify.warning({
          title: '',
          message: ''
        })
        this.$refs.title.focus()
        return false
      }
      let content = this.editor.getText()
      if (content.trim().length === 0) {
        this.$notify.warning({
          title: '',
          message: ''
        })
        return false
      }
      return true
    },
    clearArticle() {
      this.html = ''
    }
  },
  beforeDestroy() {
    const editor = this.editor
    if (editor == null) return
    editor.destroy() // 
  }
}
</script>

<style lang="stylus">
.blog-editor>.el-card
  margin-bottom 10px
  h3
    font-size 24px
    font-weight 900

// head
.editor-head--input
  padding 10px
  input, .el-input__icon.el-input__clear
    font-size 18px
  .el-input__suffix
    right 16px
    top 2px

// column
.editor-column--group
  display flex
  flex-wrap wrap
  padding 10px
  .el-radio-button, .el-radio-button:first-child, .el-radio-button:last-child
    margin 0 10px 10px 0
    .el-radio-button__inner
      border-radius 4px
      border 1px solid #ccc

// control
.blog-editor--control .el-card__body
  display flex
  .el-button
    flex 1
    font-size 18px
</style>